import React from 'react'
import App, { Container } from 'next/app'
import Router from 'next/router'
import Head from 'next/head'
import * as gtag from '../lib/gtag'
import '../styles/main.scss'

Router.events.on('routeChangeComplete', url => gtag.pageview(url));

export default class Nevek extends App {
    static async getInitialProps({ Component, router, ctx }) {
        let pageProps = {}

        if (Component.getInitialProps) {
            pageProps = await Component.getInitialProps(ctx)
        }

        return { pageProps }
    }

    updateFooter = () => {
        const main = document.querySelector('div#__next');
        const footer = document.querySelector('footer#site-footer');
        const footerHeight = footer.offsetHeight;
        const windowHeight = document.documentElement.clientHeight;
        const safetyMargin = 50;

        if((footerHeight + safetyMargin) > windowHeight){
            footer.classList.remove('fixed');
            main.style.paddingBottom = 0;
        } else {
            footer.classList.add('fixed');
            main.style.paddingBottom = footerHeight + 'px';
        }

    }

    componentDidMount() {
        this.updateFooter();
        window.addEventListener('resize', this.updateFooter);
    }

    render () {
        const { Component, pageProps } = this.props

        return (
            <Container>
                <Head>
                    <title>Keven Lefebvre | Stratège numérique à Chicoutimi, Saguenay (Québec)</title>
                </Head>
                <Component {...pageProps} />
            </Container>
        )
    }
}
